import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { mainUrl } from "../helpers/api";
import { loadSmsMobileSideBarActive } from "../redux/action/utils";
import NotificationContext from "../store/notification-context";
import "./SmsLeads.scss";

const SmsAddLead = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  // data select
  const [brands, setBrands] = useState([]);
  // const [channels, setChannels] = useState([]);

  // data
  const [label, setLabel] = useState("");
  const [name, setName] = useState("");
  // const [channel_id, setChannel_id] = useState();
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [product_brand_id, setProduct_brand_id] = useState();
  const [voucher, setVoucher] = useState("");
  const [voucher_image, setVoucher_image] = useState(null);
  const [note, setNote] = useState("");

  // validate
  // const [channelValidate, setChannelValidate] = useState(true);
  const [brandValidate, setBrandValidate] = useState(true);

  const [error, setError] = useState("");

  const [disableEmailForm, setDisableEmailForm] = useState(false);

  const notificationCtx = useContext(NotificationContext);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getBrands = async () => {
      try {
        const res = await axios.get(`${mainUrl}/sms-brands`, config);
        // console.log(res.data.data);
        setBrands(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    // const getChannels = async () => {
    //   try {
    //     const res = await axios.get(`${mainUrl}/sms-channels`, config);
    //     // console.log(res.data.data);
    //     setChannels(res.data.data);
    //   } catch (error) {
    //     console.error(error);
    //   }
    // };

    getBrands();
    // getChannels();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    // channel_id ? setChannelValidate(true) : setChannelValidate(false);
    product_brand_id ? setBrandValidate(true) : setBrandValidate(false);

    const formData = new FormData();
    // const data = JSON.stringify({
    //   label,
    //   name,
    //   // channel_id,
    //   email,
    //   phone,
    //   address,
    //   product_brand_id,
    //   note,
    //   voucher,
    // });

    voucher_image &&
      formData.append("voucher_image", voucher_image, voucher_image.name);
    label && formData.append("label", label);
    name && formData.append("name", name);
    email && formData.append("email", email);
    phone && formData.append("phone", phone);
    address && formData.append("address", address);
    product_brand_id && formData.append("product_brand_id", product_brand_id);
    note && formData.append("note", note);
    voucher && formData.append("voucher", voucher);

    if (
      label &&
      name &&
      // channel_id &&
      email &&
      phone &&
      address &&
      product_brand_id
    ) {
      notificationCtx.showNotification({
        title: "Creating Lead...",
        message: "Add Lead.",
        status: "pending",
      });
      try {
        await axios.post(`${mainUrl}/leads/add`, formData, config);
        // console.log(res.data);
        history.go(-1);
        notificationCtx.showNotification({
          title: "Success!",
          message: "Successfully Create Lead!",
          status: "success",
        });
      } catch (error) {
        console.error(error);
        setError(error.response.data.message);
        notificationCtx.showNotification({
          title: "Error!",
          message: error.message || "Something went wrong!",
          status: "error",
        });
      }
    }

    // console.log(data);
  };

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getUserExisting = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/customers/find-by-phone?phone=${phone}`,
          config
        );
        console.log(res.data);

        if (res.data.success) {
          setDisableEmailForm(true);
          setEmail(res.data.customer.email);
        } else {
          setDisableEmailForm(false);
          setEmail("");
        }
      } catch (error) {
        console.error(error);
      }
    };

    getUserExisting();
  }, [phone]);

  // useEffect(() => {
  //   console.log(disableEmailForm);
  // }, [disableEmailForm, phone]);

  return (
    <div className="sms-add-leads" id="sms-add-leads">
      <div className="sms-header d-flex ">
        <img
          onClick={() => dispatch(loadSmsMobileSideBarActive())}
          src="/Asset/Sidebar/bar.png"
          style={{ width: "23px", height: "23px" }}
          alt="bar"
        />
        <img
          src="/Asset/sms/Logo.png"
          style={{ width: "142px", margin: "0 auto" }}
          alt="logo"
        />
      </div>
      <h1
        className="text-center"
        style={{
          fontWeight: "700",
          fontSize: "18px",
          color: "#17949D",
          marginTop: "20px",
        }}
      >
        Add Lead
      </h1>
      {error !== "" ? (
        <div
          className="alert alert-danger"
          role="alert"
          onClick={() => setError("")}
        >
          {error}
        </div>
      ) : (
        ""
      )}
      <form onSubmit={(e) => handleSubmit(e)}>
        <div className="form-outline mb-3">
          <label>
            Label <span>*</span>
          </label>
          <input
            type="text"
            id="label"
            className="form-control"
            name="label"
            placeholder="Insert lead name"
            value={label}
            onChange={(e) => setLabel(e.target.value)}
            required
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Full Name <span>*</span>
          </label>
          <input
            type="text"
            id="name"
            className="form-control"
            name="name"
            placeholder="Insert lead name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        {/* <div className="form-outline mb-3">
          <label>
            Channel <span>*</span>
          </label>
          {!channelValidate && (
            <div className="alert alert-warning" role="alert">
              Channel not selected
            </div>
          )}
          <select
            className="form-select"
            aria-label="Select Channel"
            onChange={(e) => setChannel_id(parseInt(e.target.value))}
            // onChange={(e) => setChannel_id(parseInt(e.target.value))}
          >
            <option value="">Select Channel</option>
            {channels.map((channel) => (
              <option value={channel.id} key={channel.id}>
                {channel.name}
              </option>
            ))}
          </select>
        </div> */}
        <div className="form-outline mb-3">
          <label>
            Phone Number <span>*</span>
          </label>
          <input
            type="text"
            id="phone"
            className="form-control"
            name="phone"
            placeholder="Insert your Phone Number"
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            required
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Email <span>*</span>
          </label>
          <input
            type="email"
            id="email"
            className="form-control"
            name="email"
            placeholder="Insert your email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            aria-label="Disabled input example"
            disabled={disableEmailForm}
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Address <span>*</span>
          </label>
          <input
            type="text"
            id="address"
            className="form-control"
            name="address"
            placeholder="Insert your address"
            value={address}
            onChange={(e) => setAddress(e.target.value)}
            required
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Brand Recomendation <span>*</span>
          </label>
          {!brandValidate && (
            <div className="alert alert-warning" role="alert">
              Brand not selected
            </div>
          )}
          <select
            className="form-select"
            name="brand"
            onChange={(e) => setProduct_brand_id(parseInt(e.target.value))}
          >
            <option value="">Select Brand</option>
            {brands.map((brand) => (
              <option value={brand.id} key={brand.id}>
                {brand.name}
              </option>
            ))}
          </select>
        </div>

        <div className="form-outline mb-3">
          <label>
            Voucher <span>*</span>
          </label>
          <input
            type="text"
            id="voucher"
            className="form-control"
            name="voucher"
            placeholder="Insert your voucher"
            value={voucher}
            onChange={(e) => setVoucher(e.target.value)}
          />
        </div>
        <div className="form-outline mb-3">
          <label>Upload Voucher</label>
          <input
            type="file"
            id="voucher_image"
            className="form-control form-control-sm"
            name="voucher_image"
            onChange={(e) => setVoucher_image(e.target.files[0])}
          />
        </div>
        {/* <div className="form-outline mb-3">
          <label>
            Product Type <span>*</span>
          </label>
          <input
            type="text"
            id="product-type"
            className="form-control"
            name="product-type"
            placeholder="Insert your Product Type"
          />
        </div> */}
        <div className="form-outline mb-3">
          <label>Notes</label>
          <textarea
            type="email"
            id="email"
            className="form-control"
            name="email"
            placeholder="Insert your note"
            style={{ height: "100px" }}
            value={note}
            onChange={(e) => setNote(e.target.value)}
          />
        </div>
        <div className="d-flex justify-content-center">
          <button
            style={{
              padding: "10px 35px",
              background: "linear-gradient(90deg, #17949D 0%, #20B5C0 100%)",
              borderRadius: "4px",
              color: "#fff",
              width: "fit-content",
            }}
            className="btn"
          >
            + Add
          </button>
        </div>
      </form>
    </div>
  );
};

export default SmsAddLead;
